@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Mono:wght@400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2F3136;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.header-container {
    overflow: hidden;
    height: 25vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.header-logout-btn {
    position: relative;
    left: 40vw;
    bottom: 15vh;
    font-family: 'Noto Sans Mono', monospace;
    margin: 0.5rem 0;
    background-color: transparent;
    color: #24AB6D;
    border: none;
    width: 10rem;
    height: 4vh;
    font-weight: 600;
}

.header-logout-btn:hover {
    color: white;
}

.header-img {
    height: 14vh;
    cursor:pointer;
}

.main-container {
    display: flex;
    justify-content: space-between;
    height: 70vh;
    background-color: #36393F;
}



.chatroom-list-header h2{
    font-family: 'Noto Sans Mono', monospace;
    font-weight: 700;
    text-align: left;
    margin-top: 2.5rem;
}

.chatroom-list-container {
    display: flex;
    background-color: #2F3136;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    width: 25vw;
    padding-left: 3rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    border-top: 15px solid #36393F;
    border-bottom: 15px solid #36393F;
    border-top-right-radius: 100px;
    border-bottom-right-radius: 100px;
}

.chatroom-list {
    margin-top: 1rem;
}

.add-chatroom-btn {
    margin: 0.5rem 0;
    background-color: transparent;
    color: #24AB6D;
    border: 1px solid #24AB6D;
    width: 10rem;
}

.add-chatroom-btn:hover {
    color: white;
    border-color: white;
}

.create-chat-btn {
    background-color: transparent;
    color: #24AB6D;
    border: 1px solid #24AB6D;
    width: 5rem;
    padding: 0.15rem;
    margin: 0rem 0rem 0rem 0.5rem;
}

.create-chat-btn:hover {
    color:white;
    border-color:white;
}

.create-chatroom-container form {
    display: flex;
    margin-top: 1rem;
}

.create-chat-input {
    width: 8rem;
    height: 2rem;
}


.chatroom-btn {
    display: flex;
    justify-content: space-between;
    width: 13rem;
    overflow: hidden;
    border: none;
    text-align: left;
    padding: 0.15rem 0.5rem;
    font-weight: 600;
}

.chatroom-close-btn {
    position: relative;
    bottom: 3px;
    border: none;
    background-color: transparent;
    padding: 0;
    color: rgb(95, 167, 95);
}

.chatroom-close-btn:hover {
    color: white;
} 



.message-list-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    margin-bottom: 2rem;
    width: 75vw;
    overflow: hidden;
    background-color: #36393F;
}
.new-message-container form {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    margin: 2rem 0.75rem;

}

.submit-message-btn {
    background-color: transparent;
    color: #24AB6D;
    border: 1px solid #24AB6D;
    width: 9vw;
    height: 2rem;
    padding: 0.1rem;
    margin: 0rem 0rem 0rem 1vw;
}
.submit-message-btn:hover {
    background-color: transparent;
    border-color: white;
}

.enter-message-input {
    height: 2rem;
    width: 55vw;
}


.message-bubble, .message-bubble-editing {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: center;
    position: relative;
    background: transparent;
    border-radius: 0.4em;
	color: white;
	margin: 0.25rem 0.5rem 0.25rem 0rem;
    padding: 0.25rem 0.25rem 0.25rem 0.75rem;
    width: 65vw;
	text-align: left;
	border-radius: 0;
}

.message-bubble:hover, .message-bubble-editing:hover {
    background: #2F3136;
}

.message-bubble-editing {
	background-color: #2F3136;
}


.message-bubble input[type='textarea'], .message-bubble-editing input[type='textarea'] {
	border: none;
	width: 90%;
	background-color: transparent;
	color: white;
}

.message-icon-container {
	position: relative;
	bottom: 10px;
	right: 7.5px;
	display: flex;
	justify-content: flex-end;
}

.message-icon-container button {
	height: 20px;
	width: 20px;
	font-size: 1.05rem;
	background-color: transparent;
	border: none;
	color:gray;
}

.message-submit-edit-btn:hover {
    color: greenyellow;
}

.message-edit-btn:hover {
    color: goldenrod;
}

.message-close-btn:hover {
    color: #91171f;
}

.message-content-container {
	display: flex;
	flex-direction: column;
}

.message-content-author {
	color: goldenrod;
}

.footer-container {
    height: 5vh;
    
}

.footer-copyright {
    font-size: 0.75rem;
    color: gray;
    position: relative;
    top: 10px;
}
.registration-form-container {
    height: 70vh;
}

.registration-form {
    margin: 0 auto;
    width: 15rem;
}

.registration-form .form-label {
    width: 100%;
    display: inline-block;
    text-align: left;
    font-family: "Noto Sans Mono", monospace;
}

.registration-form button {
    font-family: "Noto Sans Mono", monospace;
    font-weight: 900;
    width: 50%;
}

.registration-back-btn {
    position: relative;
    right: 115px;
    border: none;
    background: transparent;
    color: white;
    font-size: 1.5rem;
}
.registration-back-btn:hover {
    color: #04aa6d;
}

.registration-submit-btn:hover {
    background-color: white;
    border: 1px solid #04aa6d;
    color: green;
}


.splash-container {
    position: relative;
    top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto;
    height: 70vh;
    width: 35rem;
    font-family: 'Noto Sans Mono', monospace;
    overflow: auto;
}

.diskord-green {
    color: #28A746;
}

.splash-container h1 {
    font-size: 3rem;
}

.splash-container button {
    font-weight: 900;
    width: 8rem;
    margin: 0.25rem 0rem;
}

.splash-container button:hover {
    background-color: white;
    border: 1px solid #04AA6D;
    color: green;
}

.splash-btn-group {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 10px;
}


.login-container {
    width: 15rem;
    margin: 0 auto;
    font-family: 'Noto Sans Mono', monospace;
}

.login-container label {
    display: inline-block;
    width: 100%;
    text-align: left;
}

.submit-login-btn {
    font-weight: 900;
    width: 6rem;
    margin-top: 0.5rem;
}

.submit-login-btn:hover {
    background-color: white;
    border: 1px solid #04aa6d;
    color: green;
}

.login-back-btn {
    position: relative;
    right: 115px;
    border: none;
    background: transparent;
    color: white;
    font-size: 1.5rem;
}

.login-back-btn:hover {
    color: #04aa6d
}

